/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.accordion-container {
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

/* 1. ESTILOS DEL ENCABEZADO */
.accordion-header {
  background-color: #f7f7f9; /* Morado Oscuro */
  color: purple;
  padding: 15px;
  width: 100%;
  text-align: left;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transition: background-color 0.3s ease;
}

.accordion-header:hover {
  background-color: #efefef;
}

/* 2. ESTILOS DEL CONTENIDO (LA CLAVE PARA LA APARICIÓN SUAVE Y LENTA) */
.accordion-content {
  /* Estado Inicial: Oculto */
  max-height: 0;
  overflow: hidden;
  /* CLAVE CSS PARA SUAVE Y LENTA: Define la transición y el tiempo a 1.2 segundos */
  transition: max-height 1.2s ease-in-out;
  background-color: #ffffff;
  color: #333;
}

.content-inner {
  padding: 20px;
  border-top: 1px solid #eee;
  /* Altura máxima antes de que aparezca el scroll */
  max-height: 700px;
  /* Permite el scroll vertical si el contenido excede 300px */
  overflow-y: auto;
  /* Esto evita que el texto quede pegado a la barra de desplazamiento */
  padding-right: 15px;
}

/* 3. ESTADO ACTIVO (Muestra el contenido con transición) */
.accordion-content.open {
  /* Valor fijo grande. El CSS anima desde 0px hasta la altura real del contenido. */
  max-height: 100rem;
}

/* Estilos para el icono de flecha */
.arrow-icon {
  /* AHORA DURA 1.2s para sincronizar con la apertura del contenido */
  transition: transform 1.2s ease-in-out;
}

.accordion-header.active .arrow-icon {
  transform: rotate(180deg);
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/operacionesIT.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 1rem 0;
}
.site-header .logo {
  max-width: 8rem;
  margin: 0;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
    max-width: 12rem;
  }
}
@media (min-width: 1024px) {
  .site-header .logo {
    max-width: 20rem;
  }
}
.site-header .contenido-header {
  padding-top: 30rem;
  padding-bottom: 5rem;
}
@media (min-width: 1024px) {
  .site-header .contenido-header {
    padding-top: 42rem;
    padding-bottom: 20rem;
  }
}
.site-header .contenido-header h1 {
  color: #FFFFFF;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .site-header .contenido-header h1 {
    font-size: 3rem;
  }
}

@media (min-width: 768px) {
  .crea-grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    max-width: 100rem;
  }
}
.crea-grid .codigoqr {
  max-width: 10rem;
}
.crea-grid h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .crea-grid h2 {
    margin-top: 0;
  }
}
.crea-grid h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

