.nuestros-ganadores {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nuestros-ganadores {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.nuestros-ganadores .posicion {
  flex-grow: 1;
  background-color: #FFFFFF;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
}
@media (min-width: 768px) {
  .nuestros-ganadores .posicion {
    border: none;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .nuestros-ganadores .posicion:hover {
    transform: scale(1.1);
  }
}
.nuestros-ganadores .posicion .felicitacion {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.nuestros-ganadores .posicion.segundolugar {
  background-image: url("../img/2lugarLosCRM.jpg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 12rem;
}
.nuestros-ganadores .posicion.primerlugar {
  background-color: #84d9bc;
  order: -1;
  background-image: url("../img/ganadoresprimerlugar.jpg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 12rem;
}
@media (min-width: 768px) {
  .nuestros-ganadores .posicion.primerlugar {
    order: unset;
    margin-top: -5rem;
    margin-bottom: -5rem;
    border-radius: 2rem;
    background-position: 50% 7rem;
    padding-top: 27rem;
    z-index: 100;
  }
}
.nuestros-ganadores .posicion.tercerlugar {
  background-image: url("../img/TodosJugadores.jpg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 12rem;
}

